@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --font-primary: 'Poppins', sans-serif;

  --colorT-lilac: rgba(176,172,251, 1);
  --colorT-lila: rgba(65,57,255, 1);
  --colorT-gris: rgba(135, 135, 148, .3);
  --colorT-rojo: rgba(245, 5, 16, 1);
  --colorT-bordo: rgba(115, 6, 11, 1);
  --colorT-celeste: rgba(52,  150,  175, 1);

  --color-blanco: #ffffff;
  --color-negro: #000000;
  --color-celeste:#ddecff;
  --color-azul: #0A4580;
  --color-gris: #6c757d;

  

}

*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--font-primary);
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}


.App{
  position: relative;
}
@media screen and (min-width: 768px){
  
}
header{
    width: 100%;
    background: var(--colorT-celeste);
    position: fixed;
}
.nav{
    width: 100%;
    height: 100%;
    background: var(--color-celeste);
    opacity: 0;
    visibility: hidden;
    transition: all ease 1s;
    position: fixed;
    transform: translateX(100%);
    /*z-index: -1000;*/
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: 100px;
    align-items: center;
    
}

.nav-options{
    display: flex;
    align-items: center;
    
}
.nav-options img{
    padding: 0px 2.2em 0px 2.2em;
}
.span{
    font-size: 1.5em;
    font-weight: 600;
    /*line-height: normal;*/
    color: var(--color-gris);
}
.span:hover{
    transition: all 0.3s ease;
    cursor: pointer;
    color: #fff;
    text-shadow:     
    0 0 5px rgba(0,178,255,1),
    0 0 10px rgba(0,178,255,1),
    0 0 20px rgba(0,178,255,1);
    
}
.toggle{
    cursor: pointer;
    width: 60px;
    height: 55px;
    position: relative;
    z-index: 1000;
}

/*--------------Creacion de la hamburguesa-----------------*/
.toggle span, .toggle span:before, .toggle span:after{
    border-radius: 100px;
    display: block;
    width: 40px;
    height: 4.5px;
    background-color: var(--color-gris);
    position: absolute;
    color: #fff;
    transition: ease .5s;
}
.toggle span:before, .toggle span:after{
    content: '';
}

.toggle span{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.toggle span:after{
    bottom: 14px;
}
.toggle span:before{
    top: 14px;
}

/*-------------------------------fin hamburguesa------------*/

/*----------------javaScript--------------------*/
.toggle.close span{
    transform: rotate(45deg);
}
.toggle.close span:before{
    top: 0px;
    transform: rotate(90deg);
}
.toggle.close span:after{
    opacity: 0;
}

.show{
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
}

@media screen and (min-width: 768px){
    header{
        background: none;
        width: 70%;
        top: 20px;
        left: 0;
        right: 0;
        margin: auto;
        
    }
    .toggle{
        display: none;
    }
    .nav{
        background: #fff;
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
        position: static;
        grid-template-columns: repeat(5, 1fr);
        grid-auto-rows: auto;
    }

    .nav-options{
        justify-content: center;
        
        
    }
    .fix-end{
        justify-self: end;
    }
    .fix-start{
        justify-self: start;
    }
    
    .span{
        font-size: 1.2em;
    }
    .span:hover{
        position: relative;
        transform: scale(1.2);
    }
    .nav-options img{
        display: none;
        
    }

}
